  flex-direction: column;
  align-items: center;
  transition: transform 0.2s;
  opacity: 0;
  box-sizing: border-box;
}

.stair-0 {
  left: calc(50% - 352px);
  top: 0;
  transform: translate(-50%, 0);
  z-index: 3;
