  top: 220px;
  transform: translate(-50%, 0);
  z-index: 1;
}

.about-section-item img.AboutUsImg {
  width: 100%;
  max-width: 220px;
  border-radius: 10px;
  margin-bottom: 16px;
  object-fit: cover;
  aspect-ratio: 1/1;
}

.about-section-text {
  color: #fff;
  text-align: left;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
}

.about-section-text h2 {
  font-size: clamp(1.1rem, 2.5vw, 1.5rem);
  margin-bottom: 8px;
  color: #e0e0e0;
}

.about-section-text p {
  font-size: clamp(0.9rem, 2vw, 1.1rem);
  color: #f5f5f5;
}

.about-cards-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 32px;
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
  position: relative;
}

@media (max-width: 1100px) {
  .about-overlap-parent {
    height: auto !important;
    min-height: unset !important;
    margin-bottom: 32px;
  }
  .about-sections-wrapper,
  .staircase-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    min-width: 0;
    max-width: 100vw;
    min-height: unset !important;
    height: auto !important;
    position: static;
  }
  .about-section-item {
    width: 95vw;
    max-width: 500px;
    min-width: unset;
    position: static;
    margin-bottom: 24px;
    height: auto;
    box-sizing: border-box;
    margin-top: 0 !important;
  }
  .about-cards-row {
    flex-direction: column;
    align-items: center;
    gap: 0;
    width: 100vw;
    max-width: 100vw;
  }
}

@media (max-width: 700px) {
  .about-section-item img.AboutUsImg {
    max-width: 90vw;
    height: auto;
  }
  .about-section-item {
    padding: 12px 4vw;
  }
}

@media (max-width: 768px) {
  .PageTwoPanel {
    flex-direction: column;
  }
  .PageTwoDivs2 {
    justify-content: center;
  }
  .AboutUsImg {
    max-width: 100%;
    height: auto;
  }
}