

.visible {
    visibility: visible;
    opacity: 1;
    transition: visibility 0s, opacity 0.5s linear;
}

.hidden {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s linear;
}

/* Desktop first, then override for smaller screens */

.body-container {
  display: flex;
  flex-wrap: wrap;
  border-radius: 10px;
  width: 100vw;
  min-width: 0;
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}

.FirstPanelImage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  height: 100vh;
  width: 100vw;
  background: whitesmoke;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box;
  margin: 0;
  position: relative;
}

.FirstPanelImage::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 80px;
  background: linear-gradient(to bottom, rgba(255,255,255,0) 0%, rgba(128,128,128,0.25) 100%);
  pointer-events: none;
  z-index: 2;
}

.FirstPanelLogo {
  display: block;
  max-width: 80vw;
  max-height: 40vh;
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.FirstPanelMessage {
  font-size: clamp(1.5rem, 5vw, 3rem);
  color: whitesmoke;
  background: rgba(128, 128, 128, 0.5);
  margin: 0;
  padding: 0.5em 1em;
  border-radius: 12px;
  text-align: center;
}

.Panel-Headings {
  font-size: clamp(2rem, 6vw, 3.5rem);
  color: black;
  margin: 20px auto;
  width: 100%;
  text-align: center;
}

.Gallery-Page {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  padding: 5vw 2vw;
  min-height: 60vh;
  width: 100%;
  background: gray;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
  border-radius: 12px;
  box-sizing: border-box;
}

.Services-Container{
    padding: 50px;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 100%;
    background: lightgray;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.Service-Items-Div{
    padding: 40px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 1fr;
    gap: 30px;
    height: auto;
    width: 100%;
    background: lightgray;
    text-align: center;
    align-items: stretch;
    justify-items: center;
}

.Services-Items{
    width: 100%;
    max-width: 300px;
    aspect-ratio: 1 / 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: White;
    font-size: calc(18px + 0.390625vw);
    border: 1px solid rgba(0, 0, 0, 0.8);
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
}

.Services-Items img{

    align-self: center;
    margin: 0 auto;
    display: block;
    background: red;
    width: 120px;
    height: 120px;
    object-fit: cover;
    border-radius: 12px;
}

@media (max-width: 1200px) {
  .body-container {
    max-width: 100vw;
    padding: 16px 4vw;
  }
}

@media (max-width: 900px) {
  .body-container {
    flex-direction: column;
    padding: 8px 2vw;
  }
  .Panel-Headings {
    font-size: clamp(1.5rem, 7vw, 2.5rem);
  }
  .Gallery-Page {
    padding: 6vw 1vw;
  }
}

@media (max-width: 600px) {
  .body-container {
    padding: 4px 1vw;
  }
  .FirstPanelImage {
    min-height: 60vh;
    height: auto;
    padding: 2vw 0;
  }
  .FirstPanelLogo {
    max-width: 95vw;
    max-height: 30vh;
  }
  .Panel-Headings {
    font-size: clamp(1.2rem, 8vw, 2rem);
  }
}

@media screen and (max-width: 789px){

    .Service-Items-Div {
        grid-template-columns: 1fr;
        gap: 10px;
    }
    .Services-Items{
        max-width: 100%;
        aspect-ratio: 1 / 1;
        height: auto;
    }
    .Panel-Headings {
        font-size: 10px;
    }
    .FirstPanelMessage{
        font-size: 50px;
    }
}

.Contact-Form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

.Contact-Form input,
.Contact-Form textarea {
  padding: 12px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  width: 100%;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}