.Services-Container {
  background: rgba(255,255,255,0.1);
  backdrop-filter: blur(8px);
  border-radius: 24px;
  box-shadow: 0 4px 32px rgba(0,0,0,0.18);
  padding: 6vw 0;
  margin: 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 40px;
  justify-items: center;
  width: 90%;
  margin: 0 auto;
  box-sizing: border-box;
}

.service-card {
  background: rgba(80,80,80,0.85);
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
  padding: 2vw 1.5vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.2s, box-shadow 0.2s;
  min-height: 240px;
  max-width: 320px;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
}

.service-card img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 12px;
  margin-bottom: 18px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
}

.service-card:hover {
  transform: translateY(-10px) scale(1.03);
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
}

.service-card h3 {
  margin: 12px 0 8px 0;
  font-size: clamp(1.1rem, 2.5vw, 1.5rem);
  color: #e0e0e0;
}

.service-card p {
  font-size: clamp(0.9rem, 2vw, 1.1rem);
  color: #f5f5f5;
  text-align: center;
}

@media (max-width: 900px) {
  .services-grid {
    grid-template-columns: 1fr;
    gap: 24px;
    width: 98%;
  }
  .service-card {
    max-width: 98vw;
    min-height: 180px;
    padding: 4vw 2vw;
  }
}
