* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  font-size: 18px;
}

@media (max-width: 900px) {
  html, body {
    font-size: 16px;
  }
}
@media (max-width: 600px) {
  html, body {
    font-size: 15px;
  }
}

.homepage-container {
  display: grid;
  grid-auto-rows: minmax(50px, auto);
  grid-template-columns: 1fr 1fr 1fr 1fr;
  width: 100vw;
  min-height: 100vh;
}

.body-container {
  width: 100vw;
  min-height: 100vh;
  border-radius: 10px;
  flex-wrap: wrap;
}

.header-nav-panel{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 30%;
    padding: 10px 0 10px 0;
    margin: 15px;
    background-color: lightgray;
}

.header-nav-panel > * {
    flex: 1;
}

.body-panel1{
    grid-row: 2;
}

.Universal-Button {
  background: #7ED957;
  width: 200px;
  max-width: 90vw;
  height: 50px;
  border-radius: 50px;
  box-shadow: 2px 4px 6px #222222;
  justify-content: center;
  color: white;
  transition: background 0.3s ease;
  font-size: 1.1rem;
}

.Universal-Button:hover {
  background: rgba(128, 128, 128, 0.5);
  cursor: pointer;
}