.Footer-Page {
  grid-column: 1 / span 4;
  min-height: 40vh;
  background-color: lightgrey;
  color: white;
  padding: 40px 10px;
  box-sizing: border-box;
  width: 100%;
}

.Footer-Content {
  background-color: #111;
  padding: 15px;
  border-radius: 15px;
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  width: 100%;
  box-sizing: border-box;
}

.Footer-Title {
  font-size: clamp(1.5rem, 4vw, 2.2rem);
  font-weight: bold;
  text-align: center;
}

.Contact-Form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  box-sizing: border-box;
}

.Contact-Form input,
.Contact-Form textarea {
  padding: 12px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  width: 100%;
  box-sizing: border-box;
}

.Contact-Form textarea {
  min-height: 80px;
  resize: vertical;
}

.Submit-Button {
  background-color: #ff6b00;
  color: white;
  padding: 12px;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.Submit-Button:hover {
  background-color: #e65c00;
}

.Footer-Copyright {
  font-size: 14px;
  text-align: center;
  opacity: 0.6;
}

@media (max-width: 900px) {
  .Footer-Content {
    max-width: 98vw;
    gap: 24px;
    padding: 10px;
  }
  .Footer-Title {
    font-size: 1.3rem;
  }
  .Contact-Form {
    max-width: 98vw;
    padding: 0 2vw;
  }
}

@media (max-width: 600px) {
  .Footer-Page {
    padding: 20px 2vw;
  }
  .Footer-Content {
    gap: 16px;
    padding: 4px;
  }
  .Footer-Title {
    font-size: 1.1rem;
  }
  .Contact-Form input,
  .Contact-Form textarea {
    font-size: 0.95rem;
    padding: 8px;
  }
}